@import "../../global.scss";

.Intro{
  background-color: white;
  display: flex;

  @include mobile{
    flex-direction: column;
    align-items: center;
  }

  .left{
    flex: 0.5;
    overflow: hidden;
    .imgContainer{
      width: 700px;
      height: 700px;
      background-color: #f4f3e1;
      border-radius: 50%;
      display: flex;
      align-items: flex-end;
      justify-content: center;
      float:right;

      @include mobile{
        align-items: start;
      }

      img{
        width: 90%;

        @include mobile{
          width: 100%;
        }
      }
    }
  }
  .right{
    flex: 0.5;
    position: relative;
    .wrapper{
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;

      @include mobile{
        padding-left: 0;
        align-items: center;
      }

      h1{
        font-size:50px;
        margin: 10px;
        @include mobile{
          font-size: 40px;
        }
      }
      h2{
        font-size: 35px;

      }
      h3{
        font-size: 30px;

        @include mobile{
          font-size: 20px;
        }

        span{
          font-size: inherit;
          color: crimson;
        }

        .ityped-cursor{
          animation: blink 1s infinite;
        }

        @keyframes blink {
          50%{
            opacity: 1;
          }
          100%{
            opacity: 0;
          }
        }
      }
    }

    a{
      position: absolute;
      bottom: 10px;
      left: 40%;
      img{
        width: 30px;
        animation: arrowBlink 2s infinite;
      }

      @keyframes arrowBlink {
        100%{
          opacity: 0;
        }  
      }
    }
  }
}